import React, { Component } from 'react';

export class Title extends Component {
    constructor(props, context){
        super(props, context);

        this.state = {
            data: 0
        };
    }    
    
    tick() {
        this.setState({
            data: this.state.data + 1  
        });
    }

    componentWillMount(){
        clearInterval(this.interval)
    }

    componentDidMount() {
        this.interval = setInterval(this.tick.bind(this), 1000);
    }

    render() {
        return(
            React.createElement("div", null, 
                React.createElement("h1", null, this.props.text, " by ", this.props.author), 
                React.createElement("h2", null, this.state.data)
            )
        );
    }
}

Title.defaultProps ={
    text: 'Hello',
    author: 'React'
}